import { PureComponent } from 'react'
import './Service.scss'
import StickySlider from '../StickySlider';

class Service extends PureComponent {

  render() {
    return (
      <div className="services">
        <div className='services__header'>
          <div className='services__header-title'>УСЛУГИ</div>
          <div className='services__header-text'>Выполняем как комплексное обследование, так и отдельные виды работ</div>
        </div>
        <div className='services__gallery'>
        <StickySlider />
        </div>
      </div>
    );
  }
}
export default Service;
