import { PureComponent } from 'react'
import './Why.scss'

class Why extends PureComponent {

  render() {
    return (
      <div className="why">
          <div className='why__work-gray'></div>
          <div className='why__work'>
              <div className='why__work-title'>Мы работаем быстро <br/>и наши отчеты проходят согласование с первого раза</div>
              <div className='why__work-text'>Над вашим объектом работает целая команда: 3 ГИПа (главных инженеров проектов) с опытом работы от 6 лет и 2 штатных сотрудника для камеральных работ</div>
          </div>
          <div className='why__work'>
              <div className='why__get'>Вы и Ваши архитекторы получаете:</div>
              <div className='why__get-item'>Отчет на который на 100% можно положиться </div>
              <div className='why__get-item'>Экономию времени и прогнозируемость сроков</div>
              <div className='why__get-item'>Предварительные результаты сразу на месте</div>
          </div>
      </div>
    );
  }
}
export default Why;
