import React, { useState, useEffect } from 'react';
import Swiper from 'react-id-swiper';
import 'swiper/swiper.scss'
import photo1 from '../../assets/img/Service/photo1.png'
import photo2 from '../../assets/img/Service/photo2.png'
import photo3 from '../../assets/img/Service/photo3.png'
import photo4 from '../../assets/img/Service/photo4.png'

const StickySlider = () => {
  const data = [
    { title: "Общее обследование", text: "Экспертиза отдельных конструкций, обмерные работы, определение прочности, расчет несущей способности и т.д.", color: photo1 },
    { title: "Тепловизионная съемка", text: "Исследование системы отопления, вентиляции и кондиционирования, а также оценка энергоэффективности здания", color: photo2 },
    { title: "Телеинспекция инженерных сетей", text: "Цветная телевизионная съемка внутренней поверхности любых протяженных объектов до 250 метров", color: photo3 },
    { title: "Тахеометрическая съемка", text: "Построение чертежей при помощи лазера, который с точностью до микрона определяет расположение объектов в пространстве", color: photo4 },
    { title: "Диагностика высотных сооружений", text: "Оценка состояния антенн и дымовых труб при помощи альпинистов и квадрокоптеров с 30-кратным зумом", color: photo1 },
  ]
  const [swiper, updateSwiper] = useState(null);
  const [translate, updateTranslate] = useState(0);
  const [transition, updateTransition] = useState(0);

  const params = {
    slidesPerView: 3,
  };

  useEffect(() => {
    if (swiper) {
      swiper.on('setTranslate', (t) => {
        updateTranslate(t);
      });
      swiper.on('setTransition', (t) => {
        updateTransition(t);
      });
    }
  }, [swiper]);

  return (
    <div>
      <Swiper className='slider' getSwiper={updateSwiper} {...params}>
        {data.map((item, idx) => (
          <div className='slider' key={idx}>
            <img className='slider__img' src={item.color} />
            <div className='slider__title'>{item.title}</div>
            <div className='slider__text'>{item.text}</div>
            <a className='slider__details' href=''>ПОДРОБНЕЕ →</a>
          </div>
        ))}
      </Swiper>
    </div>
  );
};

export default StickySlider;
