import "./Header.scss";
import burger from "../../assets/img/Header/burger.png"
import logo from "../../assets/img/Header/logo.svg"
import mail from "../../assets/img/Header/mail.svg"
import { PureComponent } from 'react'
import { render } from 'react-dom'
import CheeseburgerMenu from 'cheeseburger-menu'
import HamburgerMenu from 'react-hamburger-menu'
import MenuContent from '../ReactDimmer/menuContent'

class Header extends PureComponent {
  constructor(props) {
    super(props)

    this.state = {
      menuOpen: false,
    }
  }

  openMenu() {
    this.setState({ menuOpen: true })
  }

  closeMenu() {
    this.setState({ menuOpen: false })
  }

  render() {
    return (
      <div className="header">
        <div className="">
          <CheeseburgerMenu isOpen={this.state.menuOpen} closeCallback={this.closeMenu.bind(this)}          >
            <MenuContent closeCallback={this.closeMenu.bind(this)} />
          </CheeseburgerMenu>
          <HamburgerMenu className="header__burger"
            color="none"
            width={36}
            isOpen={this.state.menuOpen}
            menuClicked={this.openMenu.bind(this)} />
          <img className="header__logo" src={logo} alt="logo" />
        </div>
        <div className="header__connect">
          <div className="header__email">
            <div className="header__email-title">Пишите нам на email</div>
            <a className="header__email-text" href="mailto:info@diagnistics.by">
              <img className="header__email-img" src={mail}></img>
              info@diagnistics.by</a>
          </div>
          <div>
            <div className="header__phone">Есть вопросы? Звоните!</div>
            <a className="header__number" href="tel:+375294564545">+375 (29) 456-45-45</a>
          </div>
        </div>
      </div>
    );
  }
}
export default Header;
