import './Steps.scss';
import { PureComponent } from 'react'
import bg from '../../assets/img/Steps/bg.png'
import circle from '../../assets/img/Steps/circle.png'

class Steps extends PureComponent {

  render() {
    return (
      <div className="steps">
        <div className='steps__title'>Мы работаем в комплексе</div>
        <div className='steps__text'>чтобы Вы не тратили время на поиск и организацию <br />множества подрядчиков</div>
        <div className='steps__flex'>
          <img className='steps__img' src={bg} />
          <div className='steps__padding'>
          <div className='steps__item-step'>
            <span className='steps__step'>ШАГ №1.</span> <span className='steps__nameStep'> Подготовительные работы</span>
            </div>
            <div  className='steps__textStep'>Мы осматриваем здание, делаем замеры и проверяем наличие визуально заметных дефектов: трещин, коррозии, повреждения фундамента и т.д. </div>
            <div className='steps__flex'>
              <div>
                <img src={circle} />
              </div>
              <div>
                <div className="steps__item-text">“Задача этого этапа получить максимально полное представление о конструкции здания, условиях его эксплуатации и возможных слабых местах“</div>
                <span className="steps__item steps__item-upp">Алексей</span><span className="steps__item">, обследователь и главный инженер проектов</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Steps;
