import "./Intro.scss";
import { PureComponent } from 'react'
import download from '../../assets/img/Intro/download.svg'
import one from '../../assets/img/Intro/01.svg'
import two from '../../assets/img/Intro/02.svg'
import three from '../../assets/img/Intro/03.svg'
import video from '../../assets/img/Intro/video.png'

class Intro extends PureComponent {

  render() {
    return (
      <div className="intro">
        <div className="intro__container">
          <div className="intro__main">
            <div className="intro__main-title">Независимое обследование зданий и сооружений</div>
            <div className="intro__main-text">антенн, труб, теплосетей, канализаций <br /> и других объектов строительства</div>
          </div>
          <div>
            <button className="intro__button">
              <img src={download}></img>
              Получить комерческое</button>
            <div className="intro__text">Отправьте техзадание и получите коммерческое предложение в течении 15 минут на свой email или в мессенджер</div>
          </div>
        </div>
        <div className="intro__list">
          <img src={one} />
          <div className="intro__item">
            Начинаем работать без аванса
          </div>
          <img src={two} />
          <div className="intro__item">
            Предоставляем отсрочку платежа
          </div>
          <img src={three} />
          <div className="intro__item">
            Всегда называем справедливую цену
          </div>
          <a href=""> <img src={video} /></a>
        </div>
      </div>
    );
  }
}
export default Intro;
